@import url('https://fonts.googleapis.com/css?family=Open+Sans:400,600&display=swap');
@import url('https://fonts.googleapis.com/css?family=Roboto&display=swap'); 

body {
  font-family: 'Open Sans', sans-serif !important;
}

.App {
  text-align: center;
}

#root {
  display: flex;
  flex-direction: column;
  min-height: 100%;
}

.container-wrapper {
  flex: 1 0 auto;
}

.label-40 {
  color: #000;
  font-size: 40px;
}

.label-25 {
  color: #000;
  font-size: 25px;  
}

.label-30 {
  color: #000;
  font-size: 30px;  
}

.label-20 {
  color: #000;
  font-size: 20px;
}

.btn-s {
  cursor:pointer;
  display:inline-block;
  border:0;
  border-radius:2px;
  background-color:transparent;
  text-align:center;
  text-transform:uppercase;
  letter-spacing:.7px
 }

 .btn-s:focus {
  outline:0
 }

 .btn-s:disabled {
  opacity:.5
 }

 .btn_lg {
  padding:13px 55px
 }

 .btn_md {
  padding:15px 50px 12px;
  font-size:11px;
  font-weight:600;
  line-height:13px
 }

 .btn_sm {
  padding:11px 28px 10px;
  font-size:10px;
  line-height:11px;
  font-weight:700
 }

 /* #1bc281;
    #3bd684;
 */

 .btn-action {
  border: 5px;
  font-family:'Raleway-Bold','Arial',sans-serif
 }

 /* background-color: rgb(51, 233, 105); */

 .btn-green {
  background-color: rgb(33, 212, 87);
  color:#fff;
  font-weight: 100;
 }

 .btn-green:hover {
  background-color: rgb(16, 192, 69);
}

.btn-green:focus {
  background-color: rgb(16, 192, 69);
}

 .btn-blue {
  background-color: #007fad;
  color:#fff;
 }

 .btn-blue:hover {
    background-color: #359fc5;
 }

 .btn-blue:focus {
    background-color: #359fc5;
 }

.container-wrapper {
  min-height: 900px;
}

.divider {
  width: 100%;
  height: 1px;
  background-color: #cdcdcd;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-container {
  
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
